import { APIGatewayProxyEvent, APIGatewayProxyResult } from 'aws-lambda';

interface CookieOptions {
    httpOnly?: boolean;
    secure?: boolean;
    sameSite?: 'strict' | 'lax' | 'none';
    maxAge?: number;
    domain?: string;
    path?: string;
}

// Define CORS headers
const corsHeaders = {
    'Access-Control-Allow-Origin': 'https://www.asaracing.live',
    'Access-Control-Allow-Methods': '*',
    'Access-Control-Allow-Headers': '*',
    'Access-Control-Allow-Credentials': 'true',
    'Access-Control-Max-Age': '300'
};

export const handler = async (event: APIGatewayProxyEvent): Promise<APIGatewayProxyResult> => {
    // Log at the very start of the function
    console.log('Lambda started');
    console.log('Event:', JSON.stringify(event, null, 2));

    process.on('uncaughtException', (err) => {
        console.error('Uncaught Exception:', err);
    });

    process.on('unhandledRejection', (err) => {
        console.error('Unhandled Rejection:', err);
    });

    try {
        // Handle OPTIONS request
        if (event.httpMethod === 'OPTIONS') {
            return {
                statusCode: 200,
                headers: corsHeaders,
                body: ''
            };
        }

        // Handle verify request (GET)
        if (event.resource === '/auth/manage-session/verify') {
            const cookies = event.headers.Cookie || event.headers.cookie;
            return {
                statusCode: 200,
                headers: corsHeaders,
                body: JSON.stringify({
                    cookiesPresent: !!cookies,
                    cookies: cookies ? parseCookies(cookies) : null
                })
            };
        }

        // Handle session creation (POST)
        if (!event.body) {
            return {
                statusCode: 400,
                headers: corsHeaders,
                body: JSON.stringify({ message: 'Missing request body' })
            };
        }

        const { accessToken, idToken } = JSON.parse(event.body);
        if (!accessToken || !idToken) {
            return {
                statusCode: 400,
                headers: corsHeaders,
                body: JSON.stringify({ message: 'Missing required tokens' })
            };
        }

        // Set cookies
        return {
            statusCode: 200,
            headers: {
                ...corsHeaders,
                'Set-Cookie': [
                    `accessToken=${accessToken}; HttpOnly; Secure; Path=/; SameSite=None; Max-Age=3600`,
                    `idToken=${idToken}; HttpOnly; Secure; Path=/; SameSite=None; Max-Age=3600`
                ].join('; ')
            },
            body: JSON.stringify({ success: true })
        };

    } catch (error) {
        console.error('Error in handler:', error);
        return {
            statusCode: 500,
            headers: corsHeaders,
            body: JSON.stringify({ 
                message: 'Internal server error',
                error: error instanceof Error ? error.message : String(error)
            })
        };
    } finally {
        console.log('Lambda finished');
    }
};

function parseCookies(cookieString: string) {
    return cookieString.split(';')
        .map(cookie => cookie.trim())
        .reduce((acc, cookie) => {
            const [key, value] = cookie.split('=');
            acc[key] = value;
            return acc;
        }, {} as Record<string, string>);
}